@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700&display=swap);
:root {
  --firstColor: #f1f6f9;
  --secondColor: #14274e;
  --thirdColor: #394867;
  --fourthColor: #9ba4b4;

  --firstHover: #3dc8ff;

  /* < 321 - movil vertical*/
  --firstQuery: 321px; /*movil horizontal*/
  --secondQuery: 481px; /*tablet vertical*/
  --thirdQuery: 769px; /*tablet horizontal*/
  --fourthQuery: 1025px; /*portátil*/
  --fifthQuery: 1201px; /*escritorio*/
}

* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
}
body {
  background-color: #14274e;
  background-color: var(--secondColor);
  font-size: 12px;
  min-width: 250px;
}
::-webkit-scrollbar {
  display: none;
}
.container {
  margin: auto;
  max-width: 1200px;
  width: 100%;
}
.link-style {
  display: -webkit-flex;
  display: flex;
  height: auto;
  text-decoration: none;
}
.link__important {
  text-decoration: none;
  color: #3dc8ff;
  color: var(--firstHover);
  font-weight: 500;
}
.link-hover,
.link-hover-2 {
  position: relative;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 5px;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.link-hover::after {
  content: "";
  position: absolute;
  z-index: 998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  outline: 0.15rem solid #fff;
  outline-offset: -1.2rem;
  transition: all 0.4s ease-in-out;
  -webkit-transform: scale(0);
          transform: scale(0);
}
.link-hover:hover:after {
  -webkit-transform: scale(1);
          transform: scale(1);
}
.link-hover-2 i {
  position: absolute;
  color: #fff;
  transition: all 0.4s ease-in-out;
  -webkit-transform: scale(0);
          transform: scale(0);
  z-index: 997;
}
.link-hover-2:hover > i {
  -webkit-transform: scale(2);
          transform: scale(2);
}
@media screen and (min-width: 321px) {
  body {
    font-size: 14px;
  }
}
@media screen and (min-width: 481px) {
  body {
    font-size: 16px;
  }
}
@media screen and (min-width: 769px) {
  body {
    font-size: 16px;
  }
}
@media screen and (min-width: 1025px) {
  body {
    font-size: 16px;
  }
}
@media screen and (min-width: 1201px) {
  body {
    font-size: 16px;
  }
}

.header {
  -webkit-align-items: center;
          align-items: center;
  background-color: var(--secondColor);
  color: var(--firstColor);
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 999;
}
.header .container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.header__link {
  -webkit-align-items: center;
          align-items: center;
  width: -webkit-max-content;
  width: max-content;
}
.header__logo {
  width: 30px;
}
.header__title {
  color: #fff;
  font-size: 1em;
}
.menu {
  background-color: var(--secondColor);
  z-index: 999;
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
}
.menu .container {
  display: inline;
  display: initial;
}
.menu ul {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.menu ul li {
  list-style: none;
}
.menu__link {
  -webkit-align-items: center;
          align-items: center;
  border-top: 5px solid transparent;
  color: var(--firstColor);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 10px 0 5px;
}
.menu__link i {
  font-size: 1em;
}
.menu__link span {
  font-weight: lighter;
  font-size: 1em;
}
.link-active {
  border-top: 5px solid var(--firstHover);
  color: var(--firstHover);
}
.menu__link:hover {
  color: var(--firstHover);
  transition: all 0.4s ease-out;
}

@media screen and (min-width: 481px) {
  .header__logo {
    width: 40px;
  }
  .menu {
    position: relative;
  }
  .menu ul {
    display: -webkit-flex;
    display: flex;
    padding: 7.5px 0;
  }
  .menu ul li {
    margin-right: 20px;
  }
  .menu__link {
    -webkit-align-items: center;
            align-items: center;
    border-top: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 0;
  }
  .menu__link i {
    display: none;
  }
  .link-active {
    color: var(--firstHover);
    border: none;
  }
}

.background {
